@use '../abstracts' as a;

.hero-section {
	background: a.$clr-primary-color;
	padding: 10em 1.5em;
	position: relative;
	z-index: 1;
	@include a.breakpoint(laptop, min) {
		padding: 10em 5em;
	}

	.hero-wrapper {
		display: flex;
		flex-direction: column;
	}
	h2 {
		display: flex;
		color: a.$white;
		font-family: a.$MainFont;
		font-size: clamp(1rem, 4vw, 2rem);

		span {
			color: a.$clr-third;
		}

		.Typist .Cursor {
			display: inline-block;
			margin-left: 0.2em;

			&--blinking {
				opacity: 1;
				animation: blink 1s linear infinite;
				@keyframes blink {
					0% {
						opacity: 1;
					}
					50% {
						opacity: 0;
					}
					100% {
						opacity: 1;
					}
				}
			}
		}

		.Mytypist {
			display: flex;
			margin-left: 0.22em;
		}
	}

	h3 {
		text-transform: uppercase;
		font-family: a.$MainFont;
		font-weight: lighter;
		font-size: clamp(1rem, 4vw, 1.5rem);
		letter-spacing: 0.3em;
		color: a.clr(menu, dark);
	}

	h1 {
		color: a.$white;
		font-family: a.$MainFont;
		font-weight: bold;
		text-transform: uppercase;
		font-size: clamp(3rem, 10vw, 5rem);
		line-height: 1.2em;
	}

	.cta-btn {
		@extend %Button;
		&:hover::after,
		&:focus::after {
			transform: scale(1.19, 1.95);
		}
	}
}
