@use '../abstracts' as a;
.about-section {
	background: a.$clr-secondary;
	box-shadow: a.$shadow;
	padding: 3em 1.5em;

	@include a.breakpoint(laptop, min) {
		padding: 3em 5em;
	}

	.scroll-wheel {
		text-align: end;

		.wheel {
			@extend %Wheel;
		}
	}
	.subject-title-wrapper {
		display: flex;
		justify-content: center;
		margin-bottom: 2em;
		.subject-title-about {
			@include a.Title(a.$clr-third);
		}
	}

	.about-wrapper {
		@include a.breakpoint(mobile, min) {
			display: flex;
			justify-content: space-between;
		}
		.about-illustration {
			margin-bottom: 2em;
			@include a.breakpoint(mobile, min) {
				margin-bottom: 0;
			}
			.illustration-img {
				@include a.breakpoint(laptop, min) {
					width: 500px;
					height: 500px;
					margin-bottom: -7em;
				}
				width: 300px;
				height: 300px;
			}
		}
		.description {
			@include a.breakpoint(mobile, min) {
				display: flex;
				flex-direction: column;
				justify-content: center;
			}

			display: flex;
			flex-direction: column;

			h1 {
				margin-bottom: 0.5em;
			}
			p {
				max-width: 45ch;
				line-height: 1.5em;
			}

			.cv-button {
				@extend %Button;
				&:hover::after,
				&:focus::after {
					transform: scale(1.21, 1.95);
				}
			}
		}
	}
}
