@use '../abstracts' as a;

.projects-section {
	background: a.$clr-third;
	box-shadow: a.$shadow;
	padding: 3em 1.5em;

	.projects-wrapper {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(auto, 315px));
		grid-template-rows: auto;
		gap: 5em;
		justify-content: space-around;

		@include a.breakpoint(laptop, min) {
			margin-bottom: -4em;
		}
		// display: flex;
		// flex-flow: row wrap;
		// justify-content: center;
		// gap: 1em;

		.portfolio-wrapper {
			@extend %Card;

			.card-picture {
				@extend %Picture;
				.card-img {
					background: white;
					height: 100%;
					width: 100%;
					border-radius: 3em;
					z-index: -1;
				}
			}
			h1 {
				margin-bottom: 1em;
			}
			.card-wrapper {
				p {
					max-width: 34ch;
				}
				.button-wrapper {
					margin-right: auto;

					.card-button {
						@extend %Button;
						&:hover::after,
						&:focus::after {
							transform: scale(1.131, 1.95);
						}
					}
				}
			}
		}

		.database-wrapper {
			@extend %Card;
