@use '../abstracts' as a;
.contact-section {
	background: a.$clr-secondary;
	box-shadow: a.$shadow;
	padding: 3em 1.5em;
	position: relative;

	@include a.breakpoint(mobile, min) {
		padding: 3em 5em;
	}

	.subject-title-wrapper {
		display: flex;
		justify-content: center;
		margin-bottom: 2em;
		.subject-title-contact {
			@include a.Title(a.$clr-third);
		}
	}

	.contact-wrapper {
		@include a.breakpoint(mobile, min) {
			display: flex;
			justify-content: space-between;
		}
		.get-in-touch {
			.first-half {
				margin-bottom: 1em;
				h1 {
					margin-bottom: 1em;
				}
				p {
					max-width: 45ch;
				}
			}
			.second-half {
				h1 {
					font-size: 1rem;
				}

				.side-picture-name {
					@extend %SidePicture;
					&::before {
						display: flex;
						align-items: center;
						padding-top: 0.5em;
						width: 100%;
						height: 100%;
						position: absolute;
						content: url("/src/client/imgs/Name.svg");
						left: -3.5em;
					}
				}
				.side-picture-phone {
					@extend %SidePicture;
					&::before {
						display: flex;
						align-items: center;
						padding-top: 0.5em;
						width: 100%;
						height: 100%;
						position: absolute;
						content: url("/src/client/imgs/phone.svg");
						left: -3.5em;
					}
				}
				.side-picture-email {
					@extend %SidePicture;
					&::before {
						display: flex;
						align-items: center;
						padding-top: 0.5em;
						width: 100%;
						height: 100%;
						position: absolute;
						content: url("/src/client/imgs/email.svg");
						left: -3.5em;
					}
				}
			}
		}

		.send-a-message {
			@include a.breakpoint(mobile, min) {
				width: 30%;
			}
			h1 {
				margin-bottom: 1em;
			}
			.contact-form {
				display: flex;
				flex-direction: column;

				input {
					margin-bottom: 1em;
					border-radius: 0.5em;
					background: a.$clr-secondary;
					padding: 1em;
					border: 1px solid black;
				}

				textarea {
					border-radius: 0.5em;
					padding: 1em 1em;
					padding-bottom: 3em;
					background: a.$clr-secondary;
					border: 1px solid black;
				}
			}
			.form-button {
				z-index: 1;
				@extend %Button;
				&:hover::after,
				&:focus::after {
					transform: scale(1.3, 1.95);
				}
			}
		}
	}
}
