@use './colors' as a;
@use './fonts' as f;
@use './media' as m;

%SidePicture {
	position: relative;
	margin-bottom: 1em;
	margin-left: 3.5em;
}
%Picture {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 103px;
	height: 90px;
	border-radius: 3em;
	margin-top: -3em;
	margin-bottom: 3em;
	border: 3px solid a.clr(third);

	box-shadow: 1px 60px 1px 87px a.$clr-secondary;
}
%Card {
	display: flex;

	box-shadow: 0px 4px 5px 1px rgba(0, 0, 0, 0.25);
	flex-direction: column;
	align-items: center;
	line-height: 1.5em;
	position: relative;
	z-index: 1;
	padding: 1.5em 1.5em;
	border-radius: 0.5em;
	background: a.$clr-primary-color;
	@include m.breakpoint(mobile, min) {
	}
}
%Button {
	position: relative;
	padding-left: 0.5em;
	margin-top: 0.5em;
	border: none;
	background: none;
	font-size: 1rem;
	color: a.$white;
	cursor: pointer;

	&::after {
		position: absolute;
		border-radius: 0.1em;
		content: "";
		top: 10px;
		right: 5px;
		left: 1px;
		bottom: -3px;
		background: a.$clr-third;
		z-index: -1;
		transform-origin: bottom left;
		transition: transform 175ms cubic-bezier(0.91, 0.01, 0.67, 1.72);
	}
}

%Wheel {
	display: none;
	width: 50px;
	height: 50px;
	object-fit: cover;
	border-radius: 0.5em;
	//box-shadow: inset 5px 5px 10px 2px rgba(0, 0, 0, 0.25);
}
